<template>
  <ul class="collection">
    <!-- TOOD responsive image component w/ srcset -->
    <li class="responsive-image" v-for="image in images" :key="image">
      <img :src="require(`../assets${path}/${image}`)" />
    </li>
  </ul>
</template>

<script>
export default {
  name: "Collection",
  props: {
    images: { type: Array, required: true },
    path: { type: String, required: true },
  },
};
</script>

<style lang="scss" scoped>
.collection {
  columns: 29rem;
  column-gap: 0;
}

ul {
  list-style: none;
}

ul,
li {
  padding: 0;
}

.responsive-image {
  padding: 0.5rem;
}

img {
  width: 100%;
}
</style>