export default {
    props: {
        /**
         * Whether the logo should appear in the "expanded" state
         */
        expanded: {
            type: Boolean,
            default: false
        },
        /**
         * Whether the logo should appear with "dark" colors
         */
        dark: {
            type: Boolean,
            default: false
        },

        /**
         * Whether the logo should be framed by a border
         */
        frame: {
            type: Boolean,
            default: true
        },
        /**
         * Whether the logo appears flat or with a shadow
         */
        flat: {
            type: Boolean,
            default: true
        },
        /**
         * Whether to enable the default animation for the logo
         */
        animate: {
            type: Boolean,
            default: true
        },
    },
}