import {
    getCollections,
} from '@/api/collections';
import apiObjectToCollection from './factory';

const collections = {
    state: {
        collections: []
    },
    getters: {
        getCollections: state => state.collections,
        getCollectionById: state => collectionId => state.collections.find(collection => collection.id === collectionId),
    },
    mutations: {
        addCollection: (state, collection) => state.collections.push(collection),
    },
    actions: {
        createCollections: ({
            commit
        }) => {
            getCollections().forEach(result => {
                commit('addCollection', apiObjectToCollection(result));
            });
        }
    },
}

export default collections;