<template>
  <div
    class="mobile-header-container"
    :class="{ 'show-shadow': titleHasShadow }"
    v-scroll="onScroll"
  >
    <nick-anderson-logo
      class="header-logo"
      flat
      :frame="false"
      dark
      :animate="false"
    />

    <div id="page-title">
      <h1>{{ pageTitle }}</h1>
    </div>
  </div>
</template>

<script>
import NickAndersonLogo from "@/components/logo/NickAndersonLogo.vue";
export default {
  name: "MobileHeaderNav",
  components: { NickAndersonLogo },
  data() {
    return { position: 0 };
  },
  props: {
    pageTitle: {
      type: String,
      required: true,
    },
  },
  computed: {
    titleHasShadow() {
      return this.position > 100;
    },
  },
  methods: {
    onScroll() {
      this.position =
        document.body.scrollTop || document.documentElement.scrollTop;
    },
  },
};
</script>

<style lang="scss" scoped>
@use "/src/scss/globals" as globals;

.mobile-header-container {
  padding: 0rem globals.$gutter-mobile;
  position: fixed;
  z-index: 999999;
}

::v-deep .header-logo.logo {
  transition: 450ms all ease-in;
}

.show-shadow ::v-deep .header-logo.logo {
  text-shadow: 0rem -1.2rem 0px globals.$primary-color;
}
</style>