<template>
  <div
    class="logo"
    :class="{
      expanded: expanded,
      dark: dark,
      frame: frame,
      flat: flat,
      animate: animate,
    }"
  >
    <span id="name">
      <slot></slot>
    </span>
  </div>
</template>

<script>
import logo from "@/components/logo/logo.mixin.js";

export default {
  name: "Logo",
  mixins: [logo],
  data() {
    return {};
  },
};
</script>

<style lang="scss">
@use "../../scss/globals" as g;
$maxHeight: 7.1875rem;
$paddingY: 1.25rem;

#name {
  @include g.title;
  color: g.$primary-color;

  &.dark {
    color: black;
  }
}

.logo {
  box-shadow: g.$shadow;
  text-shadow: g.$shadow;
  display: inline-block;
  transition: box-shadow 500ms ease-in;
  text-align: left;
  border-width: 0rem;
  padding: 0.75rem 0rem;
  max-height: $maxHeight - $paddingY * 2;
  &.frame {
    border: 0.25rem solid g.$primary-color;
    padding: 1.25rem 2.0625rem;

    &.dark {
      border-color: black;
    }
  }

  &.animate {
    #name {
      opacity: 0;
      animation: opacity 500ms ease-in 500ms forwards;
    }

    &.expanded.frame {
      animation: expanded-grow 500ms ease-in 500ms forwards;
    }

    &:not(.expanded).frame {
      animation: default-grow 1s ease-in 1s forwards;
    }
  }
}

@keyframes default-grow {
  from {
    padding: 0rem 0rem;
  }

  to {
    padding: 1.25rem 2.0625rem;
    border-width: 0.25rem;
  }
}

@keyframes expanded-grow {
  from {
    border-width: 0rem;
    padding: 0rem 0rem;
  }

  to {
    padding: 7.8125rem 2.0625rem;
    border-width: 0.25rem;
  }
}

@keyframes opacity {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.dark {
  #name {
    color: black;
  }
}

.logo.flat {
  text-shadow: none;
  box-shadow: none;
}
</style>