<template>
  <main id="splash">
    <div class="gradient-background" :class="{ show: hover }" />
    <div class="splash-content">
      <div class="logo-and-image">
        <div id="logo-wrapper">
          <nick-anderson-logo
            expanded
            role="heading"
            :flat="false"
            aria-label="Nick Anderson Photography Welcome Page"
          />
        </div>

        <img
          :src="require('../assets/' + cover)"
          @load="showCover = true"
          v-show="showCover"
        />
      </div>
      <nav-arrow @mouseover="hover = true" @mouseout="hover = false" />
    </div>
  </main>
</template>

<script>
import NickAndersonLogo from "@/components/logo/NickAndersonLogo.vue";
import NavArrow from "@/components/nav-arrow/NavArrow.vue";
import { mapGetters } from "vuex";
export default {
  name: "Splash",
  components: {
    NavArrow,
    NickAndersonLogo,
  },
  data() {
    return {
      cover: "tree.png",
      showCover: false,
      hover: false,
    };
  },

  computed: {
    coverPath() {
      return "../assets/" + this.cover;
    },
    ...mapGetters(["isMobile"]),
  },
};
</script>

<style lang="scss" scoped>
@use "../scss/globals";
@use "../scss/media";

#splash {
  overflow: hidden;

  width: 80%;
  margin: auto;
  margin-top: auto;
  width: 100%;
  height: 100%;
}

.splash-content {
  display: grid;
  grid-template-rows: repeat(3, auto);
  height: 100%;
}

img {
  position: absolute;
  height: 88%;
  opacity: 1;
  animation: fade 700ms ease-in;
}

.gradient-background {
  width: 100%;
  position: absolute;
  background: linear-gradient(transparent, globals.$primary-color);
  bottom: 0;
  z-index: 700;
  height: 0%;
  transition: all 4000ms ease-in;
  pointer-events: none;
}

#gradient-background.show {
  height: 70%;
}
@keyframes fade {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

#logo-wrapper {
