  }
}

#nav-arrow {
  display: flex;
  transition: transform 500ms ease-in-out;
  grid-row-start: 3;
}

.logo-and-image {
  display: flex;
  align-items: center;
  flex-direction: column;
  grid-row-start: 2;
  justify-content: center;
  grid-row-end: 3;
}

#splash-background {
  background-size: contain;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
}
</style>