import collections from '@/data/collections';

const getCollections = () => collections.map(
    (collection) => ({
        id: collection.id,
        title: collection.title,
        path: collection.path,
        coverImage: collection.coverImage
    })
);

const getCollectionImages = (collectionId) => collections.find((collection) => collection.id === collectionId).images;


export {
    getCollections,
    getCollectionImages
};