import {
    xs,
    sm,
    lg
} from '@/global';

const appWindow = {
    state: {
        size: window.innerWidth,
    },
    mutations: {
        setSize: (state, size) => {
            state.size = size;
        },
    },
    getters: {
        windowBreakpoint: state => [xs, sm, lg].reduce((prev, curr) => state.size >= curr ? curr : prev),
        isMobile: (state, getters) => getters.windowBreakpoint === xs,
    },
};

export default appWindow;