<template>
  <div
    id="nav-arrow"
    :class="{ animate: animate }"
    @mouseover="$emit('mouseover')"
    @mouseout="$emit('mouseout')"
    @click="onClick"
  ></div>
</template>

<script>
export default {
  name: "NavArrow",
  props: { animate: { type: Boolean, default: true } },
  methods: {
    onClick() {
      this.$router.push({ name: "TheMainPage" });
    },
  },
};
</script>

<style lang='scss' scoped>
@use "src/scss/globals" as global;

$yAdjust: 0.0625rem;
@keyframes move {
  0% {
    transform: translateY(0rem);
  }
  45% {
    transform: translateY($yAdjust);
  }

  60% {
    transform: translateY($yAdjust);
  }
  100% {
    transform: translateY(0rem);
  }
}

.arrow {
  content: " ";
  border-left: solid 25px transparent;
  border-right: solid 25px transparent;
  border-top: solid global.$primary-color 1rem;
  display: block;
  width: 0;
  height: 0;
  filter: drop-shadow(global.$shadow);
}

#nav-arrow {
  display: flex;
  transition: transform 500ms ease-in-out;
  flex-direction: column;

  &:hover {
    cursor: pointer;
    transform: translateY(4px);
  }

  &:after {
    @extend .arrow;
    align-self: center;
  }
}
</style>