import VueRouter from 'vue-router';
import Splash from '@/views/Splash.vue';
import Collections from "@/views/Collections";
import TheMainPage from '@/views/TheMainPage.vue';
import About from "@/views/About";

const paths = {
    splash: '/welcome',
    main: '/',
    about: '/about',
    collections: '/collections'
};

const routes = [{
    path: paths.splash,
    component: Splash,
    name: 'Splash',
}, {
    path: paths.main,
    component: TheMainPage,
    name: 'TheMainPage',
    children: [{
        path: paths.about,
        component: About,
        name: 'About'
    }, {
        path: paths.collections,
        component: Collections,
        name: 'Collections'
    }],
    beforeEnter: (to, from, next) => {
        if (from.path === paths.splash) {
            next();
        } else {
            next(paths.splash);
        }
    }
}];


export default new VueRouter({
    routes,
    scrollBehavior(to) {
        if (to.hash) {
            return {
                selector: to.hash,
                behavior: 'smooth',
            }
        }
    }

})