<template>
  <div id="app">
    <transition name="slide" mode="out-in">
      <router-view></router-view>
    </transition>
  </div>
</template>

<script>
export default {
  name: "App",
  components: {},
  methods: {
    resizeHandler() {
      this.$store.commit("setSize", window.innerWidth);
    },
  },
  created() {
    window.addEventListener("resize", this.resizeHandler);
  },
};
</script>

<style lang='scss'>
@use "./scss/globals" as global;

#app {
}
body {
  padding: 0;
  margin: 0;
}
html,
body,
#app {
  height: 100%;
  font-size: global.$root-font-size;
  background-color: global.$primary-color;
}

.slide-enter-active {
  transition: all 0.3s ease;
}
.slide-leave-active {
  transition: all 0.8s cubic-bezier(1, 0.5, 0.8, 1);
  transform: translateY(-100vh);
  position: absolute !important;
}
.slide-enter {
  transform: translateY(100vh);
}
.slide-leave-to {
  transform: translateY(-100vh);
}
</style>
