<template>
  <div>
    <header-nav />
    <div class="page-content">
      <noise-filter />
      <side-nav class="side-nav" />
      <main class="main-content">
        <collection
          v-for="collection in collections"
          :key="collection.id"
          :images="getImagesByCollectionId(collection.id)"
          :path="collection.path"
        />
      </main>
    </div>
  </div>
</template>

<script>
import HeaderNav from "../components/header-nav/HeaderNav.vue";
import SideNav from "@/views/SideNav.vue";
import { mapActions, mapGetters } from "vuex";
import Collection from "@/components/Collection.vue";
import { getCollectionImages } from "@/api/collections";
import NoiseFilter from "../components/NoiseFilter.vue";

export default {
  name: "TheMainPage",
  components: { HeaderNav, SideNav, Collection, NoiseFilter },
  data() {
    return { getCollectionImages: getCollectionImages };
  },
  methods: {
    getImagesByCollectionId(collectionId) {
      return this.getCollectionImages(collectionId);
    },
    ...mapActions({
      loadCollections: "createCollections",
    }),
  },
  computed: {
    ...mapGetters({
      collections: "getCollections",
      getCollectionById: "getCollectionById",
    }),
  },
  created() {
    this.loadCollections();
  },
};
</script>

<style lang="scss" scoped>
@use "/src/scss/globals" as global;

main {
  z-index: 10000;
}

.page-content {
  display: grid;
  grid-template-columns: 1fr;
  padding: 4rem 0rem;
  position: relative;
}

@media (min-width: global.$breakpoint-sm) {
  .page-content {
    grid-template-columns: 1fr 5fr;
    column-gap: 3rem;
    padding: 16.5rem global.$gutter 0 global.$gutter;
  }
}

::v-deep #header-nav {
  top: 4.5rem;
}
</style>